
import { mapGetters, mapActions, mapMutations } from 'vuex';
import { api } from '@/utils/api.js';
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader
},
data() {
return {
eyeOpenIf:true,
grade:'',
nickName: '',
avatar: '',
infoData: '',
user_type:''
}
},
created() {
this.grade = this.$route.query.grade || '';
this.infoShow();
},
mounted() {
this.nickName = this.user.nick;
this.avatar = this.user.avatar;
var userType = this.user.user_type;
this.user_type=userType;
},
methods: {
async infoShow(){
let data = {
plat: 3,
token: this.token,
account: this.account
}
const [err, res] = await api.usermoneyprofile(data);
if(err){
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
this.infoData = res.data;
}
},
rechargeDetail(){
this.$router.push({
path: '/balance/clearness',
query: {
recordType: 1
}
});
},
withdrawDetail(){
this.$router.push({
path: '/balance/drawMinXi',
query: {
recordType: 1
}
});
},
visibleNUM(){
this.eyeOpenIf = !this.eyeOpenIf;
},
goWithdraw(){
console.log('drawIndex')
this.$router.push('/myBlack')
},
goFanLiPage(whichFanLi){
switch (whichFanLi) {
case '365推广返利':
this.$router.push({
path: 'rebateRecord',
query: {
recordType: 1
}
})
break;
case '线下推广返利':
this.$router.push({
path: 'rebateRecord',
query: {
recordType: 2
}
})
break;
case '粉丝购买商品返利':
this.$router.push({
path: 'rebateRecord',
query: {
recordType: 3
}
})
break;
}
},
goRecharge(){
var userType = this.user.user_type;
this.user_type=userType;

if (userType==3){





this.$vux.toast.text('你没有权限');
}else{
this.$router.push({
path: 'rechargeMoneyOnline',
query: {

}
});
}
}
},
computed: {
...mapGetters(['user', 'account', 'token'])
}
}



